import './main.css';
import { Elm } from './Main.elm';
import * as serviceWorker from './serviceWorker';

var app = Elm.Main.init({
  node: document.getElementById('root')
});

var mymap = L.map('mainMap').setView([23.5, 121], 8);
L.tileLayer('https://wmts.nlsc.gov.tw/wmts/PHOTO2/default/GoogleMapsCompatible/{z}/{y}/{x}').addTo(mymap);

var markerGroup;

app.ports.setView.subscribe(function(data) {
	console.log(data);
	mymap.setView.apply(mymap, data);
});
app.ports.setMarkers.subscribe(function(data) {
	console.log(data);
	const markers = data.map(function(m) {
		const opts = {
			stroke: false,
			fill: true,
			fillOpacity: 1.0,
			fillColor: m.color
		};
		let c = L.circleMarker(m.pos, opts);
		const info = `<div class="card" style="width: 18rem;">
  <img class="card-img-top" src="..." alt="Card image cap">
  <div class="card-body">
    <h5 class="card-title">${m.title}</h5>
    <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
    <a href="#" class="btn btn-primary">Go somewhere</a>
  </div>
</div>`;
		c.bindPopup(info).addTo(mymap);
		return c;
	});
	if (markerGroup) {
		markerGroup.remove();
	}
	markerGroup = L.layerGroup(markers).addTo(mymap);
});


// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
